import React, { Component } from 'react'
import Collapsible from 'react-collapsible';


import CharSideBar from '../components/CharNavBar/CharNavBar'
import SpellCard from '../components/SpellCard/SpellCard'
import AbilityCard from '../components/AbilityCard/AbilityCard'
import PCStatCardDark from '../components/PCStatCardDark/PCStatCardDark'
import PCStatCard from '../components/PCStatCard/PCStatCard'

import NavBar from "../components/NavBar/NavBar";



import charImage from '../images/ismark-2.png'
import divination from '../images/divination.jpg'
import abjuration from '../images/abjuration.jpg'
import enchantment from '../images/enchantment.jpg'
import evocation from '../images/evocation.png'
import illusion from '../images/illusion.jpg'
import necromancy from '../images/necromancy.png'
import transmutation from '../images/transmutation.jpg'
import conjuration from '../images/conjuration.jpg'

import lifeDomain from '../images/lifeDomain-2.jpg'




import './views.css';

export class Ismark extends Component {
    render() {
        return (
            <div className='standardDarkBackground'>
            <div className='ismarkBackground'>
            <NavBar />
            <CharSideBar />
            <div className='lg-pc-hdr'>
                    <div className='cards-pc'>
                        <div className='card-pc'>
                            <PCStatCard Race='Human'
                            Class='Devotion Paladin'
                            Level='3'
                            STR='15' STRMOD='+2'
                            DEX='15' DEXMOD='+2'
                            CON='14' CONMOD='+2'
                            INT='11' INTMOD='0'
                            WIS='14' WISMOD='+2'
                            CHA='17' CHAMOD='+3' />
                        </div>

                        <div className='card-pc'>
                            <div className='playerCharacters'>
                                <div className='Title-wheat'>Garyx</div>
                            </div>
                            <img className='playerImage' src={charImage} />
                        </div>

                        <div className='card-pc'>
                            <div className='desc-box'>
                                Here is some character backstory
                            </div>
                        </div>
                    </div>
                </div>

                <div className='md-pc-hdr'>                
                        <div className='card-pc'>
                            <div className='playerCharacters'>
                                <div className='Title-wheat'>Garyx</div>
                            </div>
                            <img className='playerImage' src={charImage} />
                        </div>

                        <div className='card-pc'>
                            <PCStatCard Race='Human'
                             Class='Devotion Paladin'
                             Level='3'
                             STR='15' STRMOD='+2'
                             DEX='15' DEXMOD='+2'
                             CON='14' CONMOD='+2'
                             INT='11' INTMOD='0'
                             WIS='14' WISMOD='+2'
                             CHA='17' CHAMOD='+3' />
                        </div>
                </div>

            <div className='button-group'>
            <Collapsible trigger="Abilities" triggerClassName='semi-transparent-button-title-light' triggerOpenedClassName='semi-transparent-button-title-light'>
            <ul>
                <li>
                <AbilityCard image={lifeDomain} 
                abilityName="Divine Sense"
                description="The presence of strong evil registers on your Senses like a noxious odor, and powerful good rings like heavenly music in your ears.
                 As an action, you can open your awareness to detect such forces. Until the end of your next turn, you know the location of any Celestial, fiend, or Undead
                  within 60 feet of you that is not behind total cover. You know the type (celestial, fiend, or undead) of any being whose presence you sense, but not its identity
                   (the Vampire Count Strahd von Zarovich, for instance). Within the same radius, you also detect the presence of any place or object that has been consecrated or
                    desecrated, as with the Hallow spell.
                You can use this feature a number of times equal to 1 + your Charisma modifier. When you finish a Long Rest, you regain all expended uses."
            />
                </li>
            <li>
            <AbilityCard image={lifeDomain} 
                abilityName="Lay on Hands"
                description="Your blessed touch can heal wounds. You have a pool of Healing power that replenishes when you take a Long Rest. With that pool, you
                 can restore a total number of Hit Points equal to your Paladin level x 5.
                As an action, you can touch a creature and draw power from the pool to restore a number of Hit Points to that creature, up to the maximum amount remaining in your pool.
                Alternatively, you can expend 5 Hit Points from your pool of Healing to cure the target of one disease or neutralize one poison affecting it. You can cure multiple
                 Diseases and neutralize multiple Poisons with a single use of Lay on Hands, expending Hit Points separately for each one.
                This feature has no Effect on Undead and Constructs.."
            />
            </li>
            <li>
            <AbilityCard image={lifeDomain} 
                abilityName="Channel Divinity: Sacred Weapon"
                description=" As an action, you can imbue one weapon that you are holding with positive energy, using your Channel Divinity. For 1 minute,
                 you add your Charisma modifier to attack rolls made with that weapon (with a minimum bonus of +1). The weapon also emits bright light in a 20-foot
                  radius and dim light 20 feet beyond that. If the weapon is not already magical, it becomes magical for the duration"
            />
            </li>
            <AbilityCard image={lifeDomain} 
                abilityName="Channel Divinity: Turn Unholy"
                description=" As an action, you present your holy symbol and speak a prayer censuring fiends and undead,
                 using your Channel Divinity. Each fiend or undead that can see or hear you within 30 feet of you must make
                  a Wisdom saving throw. If the creature fails its saving throw, it is turned for 1 minute or until it takes damage.
                A turned creature must spend its turns trying to move as far away from you as it can, and it can't willingly move
                 to a space within 30 feet of you. It also can’t take reactions. For its action, it can use only the Dash action or try to escape
                  from an effect that prevents it from moving. If there's nowhere to move, the creature can use the Dodge action."
            />
            </ul>
            </Collapsible>

            <Collapsible trigger="Spells" triggerClassName='semi-transparent-button-title-light' triggerOpenedClassName='semi-transparent-button-title-light'>
            
            <Collapsible trigger="1st Level Spells" triggerClassName='semi-transparent-button-light' triggerOpenedClassName='semi-transparent-button-light'>
            <ul>

            <li> 
                <SpellCard image={enchantment} 
                spellName="Command"
                castTime="Casting Time: 1 Action"
                range="Range: 60 Feet"
                duration="Duration: 1 Round"
                school="School: Enchantment"
                description="You speak a one-word command to a creature you can see within range. The target must succeed on a Wisdom saving throw or follow the command on its next turn. The spell has no effect if the target is undead, if it doesn't understand your language, or if your command is directly harmful to it.
                Some typical commands and their effects follow. You might issue a command other than one described here. If you do so, the GM determines how the target behaves. If the target can't follow your command, the spell ends.
                Approach. The target moves toward you by the shortest and most direct route, ending its turn if it moves within 5 feet of you.
                Drop. The target drops whatever it is holding and then ends its turn.
                Flee. The target spends its turn moving away from you by the fastest available means.
                Grovel. The target falls prone and then ends its turn.
                Halt. The target doesn't move and takes no actions. A flying creature stays aloft, provided that it is able to do so. If it must move to stay aloft, it flies the minimum distance needed to remain in the air.
                
                At Higher Levels. When you cast this spell using a spell slot of 2nd level or higher, you can affect one additional creature for each slot level above 1st. The creatures must be within 30 feet of each other when you target them "
            />
                </li>
               <li> 
                <SpellCard image={evocation} 
                spellName="Cure Wounds"
                castTime="Casting Time: 1 Action"
                range="Range: Touch"
                duration="Duration: Instant"
                school="School: Evocation"
                description="A creature you touch regains a number of hit points equal to 1d8 + your spellcasting ability modifier. This spell has no effect on undead or constructs.
                At Higher Levels. When you cast this spell using a spell slot of 2nd level or higher, the healing increases by 1d8 for each slot level above 1st. "
            />

                </li>

                <li>
                <SpellCard image={abjuration} 
                spellName="Shield of Faith "
                castTime="Casting Time: 1 Bonus Action"
                range="Range: 60 Feet"
                duration="Duration: Concentration, Up to 10 min"
                school="School: Abjuration"
                description="A shimmering field appears and surrounds a creature of your choice within range, granting it a +2 bonus to AC for the duration."
            />
                </li>
                <li> 
                <SpellCard image={evocation} 
                spellName="Wrathful Smite"
                castTime="Casting Time: 1 Bonus Action"
                range="Range: Self"
                duration="Duration: Concentration, Up to 1 min"
                school="School: Evocation"
                description="The next time you hit with a melee weapon attack during this spell’s duration, your attack deals an extra 1d6 psychic damage.
                 Additionally, if the target is a creature, it must make a Wisdom saving throw or be frightened of you until the spell ends. As an action,
                  the creature can make a Wisdom check against your spell save DC to steel its resolve and end this spell."
            />
                </li>

            </ul>
            </Collapsible>
           
            </Collapsible>
            </div>

        </div>
        </div>
        )
    }
}

export default Ismark